import axios from "axios"


const url = "http://localhost:8003" ;
export const Adduser= async (data)=>{
    try{
 await axios.post(`${url}/Add`,data);
    } catch (error ){
        console.log("error In the Adduser API", error.message)
    }
}


 export const getUsers = async () => {
    try {
        const res = await axios.get('http://localhost:8003/users'); 
      console.log(res.data);
      return res.data;
    } catch (error) {
      console.log('Error in the  fetching users', error.message);
    }
  };



