import React from 'react';
import { Dialog, Box } from "@mui/material";
import Manu from "./manu/Manu";
import { AccountContext } from '../../context/AccountProvider';
import EmotyChat from "./chat/EmotyChat";
import ChatBox from './chat/ChatBox';
import { useContext } from 'react';


const Chatdialog = () => {
  const { person } = useContext(AccountContext);




  const DialogStyled = {
    height: "98%",
    marginTop: "80px",
    width: "100%",
    maxWidth: "98%",
    maxHeight: "100%",
    boxShadow: "none",
    overflow: "hidden",

  };

  const boxStyle = {
    display: "flex",


  };
  const LeftBox={
minWidth:"410px",
height:"100vh",
zIndex:"-2000px"

  }

  const RightBox={
color:"red"
  }

  return (
    <div>
      <Dialog
        open={true}
        PaperProps={{ sx: DialogStyled }}
        BackdropProps={{ style: { display: "none",zIndex: "3500"  } }}
      >

      <Box style={boxStyle}>
        <Box   style={LeftBox}>
          <Manu />
        </Box>
        <Box style={RightBox}>
      
          {Object.keys(person).length ? <ChatBox/> :<EmotyChat/>}
        </Box>
      </Box>
      </Dialog>
    </div>
  );
};

export default Chatdialog;
