import React from "react";
import { emptyChatImage } from "../../../constants/Dataa";
import { Box, Typography } from "@mui/material";
import Divider from '@mui/material/Divider';

const EmotyChat = () => {
  return (
    <>
      <Box style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <Box style={{ textAlign: 'center' }}>
          <img style={{ width: '360px', height: 'auto', marginBottom: '20px',marginLeft:"200px" }} src={emptyChatImage} alt="Error" />

<Typography  style={{ justifyContent: 'center',marginLeft:"200px",color:"#41525d", fontWeight:"300",fontSize:"30px",  marginBottom:"20px",}}> WhatsApp Web </Typography>

          <Typography style={{ justifyContent: 'center',marginLeft:"200px",color:"#41525d",fontSize:"14px" }}>
            Now send and receive messages without keeping your mobile online
          </Typography>

          <Typography style={{ justifyContent: 'center',marginLeft:"200px",color:"#41525d", marginBottom:"",fontSize:"14px",marginTop:"2px"  }}>
            Use WhatsApp on up to 4 linked devices and 1 phone at the same time.
          </Typography>
          <Divider  style={{width:"540px", marginLeft:"180px",marginBottom:"60px",marginTop:"40px" ,fontWeight:"200px"}}/>
         
        </Box>
      </Box>
    </>
  );
};

export default EmotyChat;
