import React from 'react';
import Drawer from '@mui/material/Drawer';
import { Box, Typography } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import Profile from './Profile';
const DrwaerInfo = ({ open, setOpen }) => {
  const handleclose = () => {
    setOpen(false);
  };

  return (
    <Drawer
      open={open}
      onClose={handleclose}
      PaperProps={{ style: { width: '31%', height: '95vh', left: '20px', top: '30px', background: "", boxShadow: "none" } }}
      style={{ zIndex: 1500 }}
    >
      <Box style={{ display: 'flex', alignItems: 'center', gap:"5px", padding: '10px', background: '#008069', height: '96px' }}>
        <ArrowBackIcon onClick={() => setOpen(false)} style={{ color: 'white', marginRight: '10px',marginTop:"auto" }} />
        <Typography style={{ color: 'white',fontSize:"20px",  marginTop:"auto"}}>Profile</Typography>
      </Box>
<Box   style={{background:"#ededed", height:"84vh"}}>

<Profile/>

</Box>

    </Drawer>
  );
};

export default DrwaerInfo;
