import React from 'react';
import { Box, Typography } from '@mui/material';
import MoreVert from '@mui/icons-material/MoreVert';
import Search from '@mui/icons-material/Search';
import { useContext } from 'react';
import { AccountContext } from '../../../context/AccountProvider';

const ChatHeader = ({ person }) => {
const {activeUsers}=useContext(AccountContext)

  const MainBox = {
    height: "50px",
    width: "140vh", // Corrected the width value
    background: "#ededed",
    padding: "8px 16px",
    display: "flex",
    alignItems: "center", // Corrected the property name
  };

  const Img = {
    width: "40px",
    marginTop: "5px",
    height: "40px",
    borderRadius: "50%",
    objectFit: "cover",
  };

  const ImgSide = {
    marginLeft: "12px",
    color: "black",
    marginTop: "6px",
  };

  const IconsChange = {
    marginLeft: "auto",
  };

  const ChildIcons = {
    color: "black",
  };

  return (
    <div>
      <Box style={MainBox}>
        <img style={Img} src={person.picture} alt="dp" />
        <Box>
          <Typography style={ImgSide}>{person.name}</Typography>
          <Typography style={{ marginLeft: "12px", color: "rgb(0, 0, 0, 0.6)", fontSize: "12px" }}>
  {activeUsers?.find(user => user.sub === person.sub) ? 'Online' : 'Offline'}
</Typography>

        </Box>
        <Box style={IconsChange}>
          <Search style={ChildIcons} />
          <MoreVert style={{ color: "black", marginLeft: "9px", marginTop: "7px" }} />
        </Box>
      </Box>
    </div>
  );
}

export default ChatHeader;
