import React, { useState,useContext } from 'react'

import { useEffect } from 'react'
import { Box } from '@mui/material';
import Divider from '@mui/material/Divider';
import { getUsers } from '../../service/Api';
import { AccountContext } from '../../../context/AccountProvider';


// componnent
import Converstaion2 from './Converstaion2';


const MoreLogin={
    height:"81vh",
    overflow:"overlay"
}

const Conversation = ({text}) => {
const[users,setUsers]=useState([]);
const{account,socket,setActiveUsers}=useContext(AccountContext)
    useEffect(() => {
        const fetchData = async() => {
            let res= await getUsers();
            const filterData = res.filter(user => user.name.toLowerCase().includes(text.toLowerCase()));

            setUsers(filterData)
        };
      
        fetchData();
      }, [text]);
      
      useEffect(() => {
        socket.current.emit('Adduser', account);
        socket.current.on('getUsers', users => {
          setActiveUsers(users);
        });
      }, [account, setActiveUsers, socket]);
      


  return (
    <div>
<Box style={MoreLogin}>

{users.map((user) => (
  user.sub !== account.sub && (
    <>
      <Converstaion2 key={user.sub} user={user} />
      <Divider style={{ margin: "0 0 0 70px", background: "#e9edef", opacity: "0.6" }} />
    </>
  )
))}


</Box>


    </div>
  )
}

export default Conversation
