import React, { useContext, useState } from 'react';
import { AccountContext } from '../../../context/AccountProvider';
import { Box } from "@mui/material";
import SettingsBackupRestoreIcon from '@mui/icons-material/SettingsBackupRestore';
import ChatIcon from '@mui/icons-material/Chat';
import HeaderManu from "./HeaderManu";
import DrwaerInfo from '../../Drawer/DrwaerInfo';

const header = {
  background: "#ededed",
  height: "50px",
  padding: "8px 16px",
  display: "flex",
  alignItems: "center", // Center vertically
  justifyContent: "space-between", // Add space between elements
};

const imgStyle = {
  width: "40px",
  height: "38px",
  borderRadius: "20px",
};

const iconContainer = {
  display: "flex",
  gap: "14px", // Space between icons
};

const Header = () => {
  const { account } = useContext(AccountContext);
  const [opendrawer, setOpenDrawer] = useState(false);

  const toggleDrawer = () => {
    setOpenDrawer(!opendrawer);
  };

  return (
    <>
      {account && (
        <Box style={header}>
          <img
            style={imgStyle}
            src={account.picture}
            alt="Profile"
            onClick={toggleDrawer}
          />
          <Box style={iconContainer}>
            <SettingsBackupRestoreIcon style={{ marginRight: "8px", marginTop: "1px" }} />
            <ChatIcon style={{ marginTop: "2px" }} />
            <HeaderManu  setOpenDrawer={setOpenDrawer} />
          </Box>
        </Box>
      )}
      <DrwaerInfo open={opendrawer} setOpen={setOpenDrawer} style={{background:"green"}}  />
    </>
  );
}

export default Header;
