import React, { useContext } from 'react';
import { Box, Typography } from "@mui/material";
import { AccountContext } from '../../context/AccountProvider';

const Profile = () => {
  const { account } = useContext(AccountContext);

  const imageStyles = {
    width: '170px',
    height: '170px',
    marginTop:"13px",
    borderRadius: '50%',
    display: 'block',
    margin: 'auto',
  };
  const NameCustom={
    marginTop:"10px",
    background:"#FFFFFF",
 padding:"12px 30px 3px",
 boxShadow:"0px 1px 3px rgba(0,0,0,0.08)"
  }

  return (
    <>
      <Box>
        <img src={account.picture} alt="dp" style={imageStyles} />
      </Box>
      <Box  style={NameCustom}>

<Typography  style={{fontSize:"14px",color:"#009688"}}> Your Name </Typography>
<Typography style={{fontSize:"14px",marginTop:"10px"}} > { account.name}</Typography>

      </Box>
      <Box>

<Typography  style={{padding:"15px 20px 20px 30px", fontSize:"13px",color:"#8696a0"}} >
    This is not your username or pin , This name will be visible to your whatsapp contacts
</Typography>

      </Box>
      <Box style={NameCustom}>
<Typography  style={{fontSize:"14px",color:"#009688"}}> About </Typography>
<Typography style={{fontSize:"14px",marginTop:"10px"}} > Good Things Take a Time </Typography>
       
      </Box>
    </>
  );
};

export default Profile;
