import { Box, Typography } from "@mui/material";
import React, { useContext } from "react";
import { FormatDate } from "../../../utils/CommonUtils";
import { AccountContext } from "../../../context/AccountProvider";

const ownMsg = {
    background: "#dcf8c6",
    maxWidth: "50%",
    marginLeft: "auto",
    padding: "5px",
    marginRight: "40px",
    width: "fit-content",
    display: "flex",
    borderRadius: "10px",
    color: "black",
    wordBreak: "break-word",
};

const Wrapper = {
    background: "#FFFFFF",
    maxWidth: "60%",
    padding: "5px",
    width: "fit-content",
    display: "flex",
    borderRadius: "10px",
    wordBreak: "break-word",
    color:"black",
    marginLeft:"60px",
   
   

};

const MyownText = {
    fontSize: "14px",
    padding: "0 25px 0 5px",
};

const MyTime = {
    fontSize: "10px",
    color: "#919191",
    marginTop: "6px", 
    wordBreak: "keep-all",
};

const Message2 = ({ message }) => {
    const { account } = useContext(AccountContext);

    return (
        <>
            {account.sub === message.senderId ? (
                <Box style={ownMsg}>
              <Box style={{padding:"4px "}}>
                    <Typography style={MyownText}>{message.text}</Typography>

              </Box>
                    <Typography style={MyTime}>{FormatDate(message.createdAt)}</Typography>
                </Box>
            ) : (
                <Box style={Wrapper}>
                    <Typography style={MyownText}>{message.text}</Typography>
                    <Typography style={MyTime}>{FormatDate(message.createdAt)}</Typography>
                </Box>
            )}
        </>
    );
};

export default Message2;
