
import { useContext,useEffect,useState } from 'react';
import React from 'react'
import { AccountContext } from '../../../context/AccountProvider';
import { Box } from '@mui/material';
import {Typography} from '@mui/material';
import { setConversation2,getConversation2 } from '../../service/Api';
// import {FormatDate} from "../../../utils/CommonUtils";
import { FormatDate } from '../../../utils/CommonUtils';


const Chat={
    marginTop:"10px",

display:"flex",

}
const imgStyle = {
    width: "40px",
    height: "40px",  // Fix the typo here
    borderRadius: "50%",
    padding: "0 14px",
  };
  

const NameStyling={
    marginTop:"2px",
    fontSize:"14px",
    marginLeft:"-4px"
}


const Converstaion2 = ({user}) => {

const{ setPerson, account,newMessageFlag }=useContext(AccountContext);

// This is ADD now
const[message,setMessage]=useState({});

useEffect(() => {
    const getConversationDetails = async () => {
        try {
            const data = await getConversation2({ senderId: account.sub, reciverId: user.sub });
            console.log("API Response:", data);
            setMessage({ text: data?.message, timestamp: data?.updatedAt });
        } catch (error) {
            console.error("API Error:", error);
        }
    };

    getConversationDetails();
}, [newMessageFlag]);

// ..........................................................................
    const getuser = async() => {
     setPerson(user);
     await setConversation2({senderId: account.sub, reciverId:user.sub})
    }
      
  return (
   

    <>
<Box onClick={() => getuser()} style={Chat}>


<Box>
<img style={imgStyle}  src={user.picture} alt="error" srcset="" />
</Box>
<Box>
<Typography  style={NameStyling}>
    {user.name}
    {/* ........................ */}


    {
    message?.timestamp && (
        <Typography>
       
  {message?.timestamp ? FormatDate(message.timestamp, true) : "Date not available"}


</Typography>


    )
}


</Typography>

</Box>


   <Box>

<Typography style={{marginTop:"25px",marginLeft:"-95px",fontSize:"12px",width:"100px",color:"#00000099"}}> {message.text}</Typography>




</Box>
   </Box>
</>
    
  )
}

export default Converstaion2
