import React, { useContext } from "react";
import { AccountContext } from "../../context/AccountProvider";
import { GoogleLogin } from "@react-oauth/google";
import { jwtDecode } from 'jwt-decode';
import { Dialog, Typography, Box, List, ListItem } from "@mui/material";
import { qrCodeImage } from "../../constants/Dataa";
import { Adduser } from "../service/Api";

const DialogStyled = {
  height: "98%",
  marginTop: "200px",
  width: "100%",
  maxWidth: "60%",
  maxHeight: "100%",
  boxShadow: "none",
  overFlow: "hidden",
};

function LoginDialog() {
  const { setAccount } = useContext(AccountContext);

  const onLoginSuccess =async (res) => {
    const decode = jwtDecode(res.credential); // Assuming accessToken contains the JWT token
    setAccount(decode);
    console.log(decode);
   await Adduser(decode);

  };

  const onLoginError = (res) => {
    console.log(res);
  };

  return (
    <div>
   
   <Dialog open={true} PaperProps={{ sx: DialogStyled }} BackdropProps={{ style: { display: 'none' } }}>



<Box style={{ display: "flex" }}>
  <Typography style={{ marginTop: "30px" }}>
    <p
      style={{
        fontWeight: "300",
        color: "#525252",
        fontSize: "24px",
        marginLeft: "40px",
        marginTop: "40px",
      }}
    >
      To use WhatsApp on your computer:
    </p>
    <List style={{ marginLeft: "30px" }}>
      <ListItem>1. Open WhatsApp on your Phone</ListItem>
      <ListItem>
        2. Tap{" "}
        <span
          style={{
            fontWeight: "bold",
            marginLeft: "3px",
            marginRight: "3px",
          }}
        >
          Menu
        </span>{" "}
        or{" "}
        <span
          style={{
            fontWeight: "bold",
            marginLeft: "3px",
            marginRight: "3px",
          }}
        >
          Setting
        </span>{" "}
        and select{" "}
        <span style={{ fontWeight: "bold", marginLeft: "3px" }}>
          {" "}
          Link Device
        </span>
      </ListItem>
      <ListItem>
        3. Point your phone to your screen to capture the code
      </ListItem>
    </List>
  </Typography>
  <Box style={{ position: "relative", display: "flex", height: "340px" }}>
    <img
      src={qrCodeImage}
      alt="QR code"
      style={{ marginLeft: "34px", marginTop: "45px" }}
    />
  </Box>
</Box>
<Box style={{ width: "200px", marginLeft: "530px", position: "absolute", marginTop: "167px" }}>
  <GoogleLogin onSuccess={onLoginSuccess} onError={onLoginError} />
</Box>
</Dialog>
    </div>
  );
}

export default LoginDialog;
