import React, { useContext } from 'react';
import LoginDialog from './account/LoginDialog';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Chatdialog from './chat/Chatdialog';
import { AccountContext } from '../context/AccountProvider';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
function Messengar() {
  const {account}= useContext(AccountContext);
  return (
    <div>
      
      {account ? (
        <Box style={{ background: "#EBE5DE", height: "100vh" }}>

        <AppBar
        style={{
          background: "#128C7E",
          height: "120px",
          borderRadius: "none",
          boxShadow: "none",
        }}
      >
        <Toolbar>
        <Chatdialog />
        </Toolbar>
      </AppBar> 
</Box>
 
) : (
  <>
    <Box style={{ background: "#EBE5DE", height: "100vh" }}>
      <AppBar
        style={{
          background: "#00a884",
          height: "150px",
          borderRadius: "none",
          boxShadow: "none",
        }}
      >
        <Toolbar>
        <WhatsAppIcon  style={{fontSize:"40px", marginLeft:"250px" ,marginTop:"20px" }}    />
     <p style={{fontWeight:"bold", marginLeft:"10px",marginTop:"34px"}} >WHATSAPP WEB </p>
        </Toolbar>
      </AppBar>
    </Box>
    <LoginDialog />
  </>
)}

      
    </div>
  );
}

export default Messengar;
