import React from 'react';
import EmojiEmotionsOutlinedIcon from '@mui/icons-material/EmojiEmotionsOutlined';
import AttachFileOutlinedIcon from '@mui/icons-material/AttachFileOutlined';
import MicOutlinedIcon from '@mui/icons-material/MicOutlined';
import { InputBase, Box } from '@mui/material';
import { useEffect } from 'react';
import { uploadFile } from '../../service/Api';

const container = {
  display: 'flex',
  alignItems: 'center',
  background: '#ededed',
  padding: '0 15px',
  height: '45px',
  color: '#919191',
  marginLeft: '3px',
  gap: '12px',
};

const Footer = ({ sendText, setValue, value, setFile, file, setImage }) => {
  useEffect(() => {
    const getImage = async () => {
      if (file) {
        const data = new FormData();
        data.append('name', file.name);
        data.append('file', file);

        try {
          const res = await uploadFile(data);
          console.log(res);

          // Ensure that 'data' property exists in the response
          if (res.data) {
            setImage(res.data);
          } else {
            console.error('Invalid response format:', res);
          }
        } catch (error) {
          console.error('Error in getImage:', error);
        }
      }
    };

    getImage();
  }, [file, setImage]);

  const onFileChange = (e) => {
    setFile(e.target.files[0]);
    setValue(e.target.files[0].name);
  };
  return (
    <Box style={container}>
      <EmojiEmotionsOutlinedIcon />
      <label htmlFor='fileinput'>
        <AttachFileOutlinedIcon style={{ transform: 'rotate(45deg)', marginTop: '5px' }} />
      </label>

      <input
        type='file'
        id='fileinput'
        onChange={(e) => onFileChange(e)}
        style={{ display: 'none' }}
      />

      <Box>
        <InputBase
          sx={{
            width: '120vh',
            background: '#FFFFFF',
            padding: '1px 15px',

            borderRadius: '20px', // Apply the borderRadius directly using sx prop
          }}
          placeholder='Type a message'
          onChange={(e) => setValue(e.target.value)}
          onKeyPress={(e) => sendText(e)}
          value={value}
        />
      </Box>
      <MicOutlinedIcon />
    </Box>
  );
};

export default Footer;
