import React, { useContext, useEffect,useRef } from 'react';
import { AccountContext } from '../../../context/AccountProvider';
import { Box } from '@mui/material';
import Footer from './Footer';
import { useState } from 'react';
import Message2 from './Message2';
import { getMessages, newMessage } from '../../service/Api';
import "./Message.css";

const ParentBox = {
  backgroundImage: `url('https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png')`,
};






const Message = ({ person, conversation }) => {
  const messageContainerRef = useRef(null);
    const [value, setValue] = useState('');
    const [messages,setmessages]=useState([]);
    // const[newMessageFlag,setNewMessageFlag]=useState(false);
    const[file,setFile]=useState();
    const[image,setImage]=useState('');
  const[incomingMessage,setIncomingMessage]=useState(null);

  const { account,socket,newMessageFlag ,setNewMessageFlag} = useContext(AccountContext);
  
  useEffect(() => {
    if (messageContainerRef.current) {
      // Scroll to the bottom when new messages are received or sent
      messageContainerRef.current.scrollTop = messageContainerRef.current.scrollHeight;
    }
  }, [messages, incomingMessage]); // Include incomingMessage in the dependency array
  

  


useEffect(()=>{
socket.current.on('getMessage',data=>{
  setIncomingMessage({
    ...data,
    createdAt:Date.now()
  })
})
},[socket])

useEffect(()=>{
  incomingMessage && conversation?.members?.includes(incomingMessage.senderId) &&
  setmessages(prev => [...prev,incomingMessage])
},[incomingMessage,conversation])



  const sendText = async (e) => {
    const code = e.keyCode || e.which;
    if (code === 13 ) {
      let message={};
      if(!file){
        message = {
          senderId: account.sub,
          reciverId: person.sub,
        conversationId: conversation._id,
        type: 'text',
        text: value,
      };
    } else{
      message = {
        senderId: account.sub,
        reciverId: person.sub,
      conversationId: conversation._id,
      type: 'file',
      text: image,
    };
    }

    socket.current.emit('sendMessage',message)

      console.log(message);
      await newMessage(message);
      setValue('');
      setImage('');
      setFile('');

      setNewMessageFlag(prev => !prev)
    }
  };





  const conversationId = conversation && conversation._id;
  useEffect(() => {
    const messageDetails = async () => {
      if (conversationId) {
        let data = await getMessages(conversationId);
        console.log(data);
        setmessages(data);
      }
    };
  
    messageDetails();
  }, [person._id, conversationId,newMessageFlag]);
  // there also newMessageFlag




  
  useEffect(() => {
    // Reset value after state has been updated
    setValue('');
  }, [conversation._id]); // Reset when conversation ID changes

  return (
    <>
 <Box style={ParentBox}>
  <Box 
    style={{
      overflowY: 'scroll',
      height: '77vh',
      width: '144vh',
      scrollbarWidth: 'thin',
      WebkitOverflowScrolling: 'touch',
    }}
    className="custom-scrollbar" >
    {messages && messages.map((message) => (
<Box  style={{padding:"2px"}}>
      <Message2  message={message} />

</Box>
    ))}
  </Box>
</Box>


      <Footer sendText={sendText} setValue={setValue} value={value} 
        file={file} setFile={setFile} setImage={setImage}  // Make sure setImage is a function
      />
    </>
  );
};

export default Message;
